<template>
  <view class="evaluations-container">
    <!-- È™ëÊâãÂØºËà™Ê†è -->
    <rider-nav current-page="evaluations"></rider-nav>

    <!-- È°∂ÈÉ®Á≠õÈÄâÊ†è -->
    <view class="filter-bar">
      <view
        class="filter-item"
        :class="{ active: activeRating === null }"
        @tap="switchRating(null)"
      >
        <text class="filter-text">ÂÖ®ÈÉ®</text>
      </view>
      <view
        class="filter-item"
        :class="{ active: activeRating === 5 }"
        @tap="switchRating(5)"
      >
        <text class="filter-text">5Êòü</text>
      </view>
      <view
        class="filter-item"
        :class="{ active: activeRating === 4 }"
        @tap="switchRating(4)"
      >
        <text class="filter-text">4Êòü</text>
      </view>
      <view
        class="filter-item"
        :class="{ active: activeRating === 3 }"
        @tap="switchRating(3)"
      >
        <text class="filter-text">3Êòü</text>
      </view>
      <view
        class="filter-item"
        :class="{ active: activeRating === 2 }"
        @tap="switchRating(2)"
      >
        <text class="filter-text">2Êòü</text>
      </view>
      <view
        class="filter-item"
        :class="{ active: activeRating === 1 }"
        @tap="switchRating(1)"
      >
        <text class="filter-text">1Êòü</text>
      </view>
    </view>

    <!-- ËØÑ‰ª∑ÂàóË°® -->
    <scroll-view
      class="evaluations-scroll"
      scroll-y
      @scrolltolower="loadMore"
      refresher-enabled
      :refresher-triggered="refreshing"
      @refresherrefresh="onRefresh"
    >
      <view class="evaluation-list" v-if="evaluationList.length > 0">
        <view
          class="evaluation-card"
          v-for="evaluation in evaluationList"
          :key="evaluation.orderId"
        >
          <!-- Áî®Êà∑‰ø°ÊÅØ -->
          <view class="user-section">
            <view class="user-avatar">
              <text>{{ evaluation.userName ? evaluation.userName.substring(0, 1) : 'Áî®' }}</text>
            </view>
            <view class="user-info">
              <text class="user-name">{{ evaluation.userName || 'Áî®Êà∑' }}</text>
              <text class="eval-time">{{ formatTime(evaluation.evaluationTime) }}</text>
            </view>
          </view>

          <!-- ËØÑÂàÜ -->
          <view class="rating-section">
            <view class="rating-stars">
              <text
                class="star-icon"
                v-for="(star, index) in 5"
                :key="index"
              >
                {{ index < evaluation.rating ? '‚òÖ' : '‚òÜ' }}
              </text>
            </view>
            <text class="rating-score">{{ evaluation.rating }}ÂàÜ</text>
          </view>

          <!-- ËØÑ‰ª∑Ê†áÁ≠æ -->
          <view class="tags-section" v-if="getTags(evaluation.feedback).length > 0">
            <view
              class="tag-item"
              v-for="(tag, index) in getTags(evaluation.feedback)"
              :key="index"
            >
              {{ tag }}
            </view>
          </view>

          <!-- ËØÑ‰ª∑ÂÜÖÂÆπ -->
          <view class="content-section">
            <text class="content-text">{{ getFeedbackText(evaluation.feedback) }}</text>
          </view>

          <!-- ËÆ¢Âçï‰ø°ÊÅØ -->
          <view class="order-section">
            <view class="order-info">
              <text class="order-label">ËÆ¢ÂçïÂè∑Ôºö</text>
              <text class="order-value">{{ evaluation.orderNo }}</text>
            </view>
            <view class="order-info">
              <text class="order-label">ÊúçÂä°Á±ªÂûãÔºö</text>
              <text class="order-value">{{ getServiceTypeName(evaluation.serviceType) }}</text>
            </view>
          </view>

          <!-- È™ëÊâãÂõûÂ§çÂå∫Âüü -->
          <view class="reply-section" v-if="evaluation.riderReply">
            <view class="reply-header">
              <text class="reply-title">ÊàëÁöÑÂõûÂ§ç</text>
              <text class="reply-time">{{ formatTime(evaluation.replyTime) }}</text>
            </view>
            <text class="reply-content">{{ evaluation.riderReply }}</text>
          </view>

          <!-- ÂõûÂ§çÊåâÈíÆ -->
          <view class="action-section" v-else>
            <button class="btn-reply" @click.stop="replyEvaluation(evaluation.orderId)">
              ÂõûÂ§çËØÑ‰ª∑
            </button>
          </view>
        </view>
      </view>

      <!-- Âä†ËΩΩÊõ¥Â§ö -->
      <view class="load-more" v-if="evaluationList.length > 0">
        <text v-if="loading">Âä†ËΩΩ‰∏≠...</text>
        <text v-else-if="hasMore">‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö</text>
        <text v-else>Ê≤°ÊúâÊõ¥Â§ö‰∫Ü</text>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-if="evaluationList.length === 0 && !loading">
        <text class="empty-icon">üìù</text>
        <text class="empty-title">ÊöÇÊó†ËØÑ‰ª∑</text>
        <text class="empty-desc">ÂÆåÊàêËÆ¢ÂçïÂêéÂ∞±‰ºöÊî∂Âà∞Áî®Êà∑ËØÑ‰ª∑Âï¶~</text>
      </view>
    </scroll-view>
  </view>
</template>

<script>
import RiderNav from '@/components/rider-nav/rider-nav.vue'
import { getRiderEvaluations } from '@/api/rider.js'

export default {
  components: {
    RiderNav
  },

  data() {
    return {
      activeRating: null, // null=ÂÖ®ÈÉ®, 1-5Êòü
      evaluationList: [],
      loading: false,
      refreshing: false,
      hasMore: true,
      currentPage: 1,
      pageSize: 10
    }
  },

  onLoad() {
    this.loadEvaluations()
  },

  methods: {
    /**
     * ÂàáÊç¢ËØÑÂàÜÁ≠õÈÄâ
     */
    switchRating(rating) {
      this.activeRating = rating
      this.currentPage = 1
      this.evaluationList = []
      this.hasMore = true
      this.loadEvaluations()
    },

    /**
     * Âä†ËΩΩËØÑ‰ª∑ÂàóË°®
     */
    async loadEvaluations() {
      if (this.loading) return

      try {
        this.loading = true

        const params = {
          type: 'received', // Êî∂Âà∞ÁöÑËØÑ‰ª∑
          page: this.currentPage,
          size: this.pageSize
        }

        // Ê∑ªÂä†ËØÑÂàÜÁ≠õÈÄâ
        if (this.activeRating !== null) {
          params.rating = this.activeRating
        }

        const result = await getRiderEvaluations(params)

        if (result.code === 200 && result.data) {
          const newEvaluations = result.data.records || []

          if (this.currentPage === 1) {
            this.evaluationList = newEvaluations
          } else {
            this.evaluationList = [...this.evaluationList, ...newEvaluations]
          }

          // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§ö
          this.hasMore = newEvaluations.length >= this.pageSize
        }

      } catch (error) {
        console.error('Ëé∑ÂèñËØÑ‰ª∑ÂàóË°®Â§±Ë¥•:', error)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      } finally {
        this.loading = false
        this.refreshing = false
      }
    },

    /**
     * Âä†ËΩΩÊõ¥Â§ö
     */
    loadMore() {
      if (!this.hasMore || this.loading) return

      this.currentPage++
      this.loadEvaluations()
    },

    /**
     * ‰∏ãÊãâÂà∑Êñ∞
     */
    onRefresh() {
      this.refreshing = true
      this.currentPage = 1
      this.loadEvaluations()
    },

    /**
     * ‰ªéËØÑ‰ª∑ÂÜÖÂÆπ‰∏≠ÊèêÂèñÊ†áÁ≠æ
     */
    getTags(feedback) {
      if (!feedback) return []
      const tagMatch = feedback.match(/\[(.*?)\]/)
      if (tagMatch && tagMatch[1]) {
        return tagMatch[1].split(',').map(tag => tag.trim()).filter(tag => tag)
      }
      return []
    },

    /**
     * Ëé∑ÂèñÁ∫ØÊñáÊú¨ËØÑ‰ª∑ÂÜÖÂÆπ
     */
    getFeedbackText(feedback) {
      if (!feedback) return ''
      return feedback.replace(/\[.*?\]\s*/, '').trim()
    },

    /**
     * Ëé∑ÂèñÊúçÂä°Á±ªÂûãÂêçÁß∞
     */
    getServiceTypeName(type) {
      const types = {
        1: 'Â∏Æ‰π∞',
        2: 'Â∏ÆÈÄÅ',
        3: 'Â∏ÆÂèñ',
        4: 'ÂÖ®ËÉΩ'
      }
      return types[type] || 'Êú™Áü•'
    },

    /**
     * Ê†ºÂºèÂåñÊó∂Èó¥
     */
    formatTime(time) {
      if (!time) return '-'
      const date = new Date(time)
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hours = String(date.getHours()).padStart(2, '0')
      const minutes = String(date.getMinutes()).padStart(2, '0')
      return `${year}-${month}-${day} ${hours}:${minutes}`
    },

    /**
     * ÂõûÂ§çËØÑ‰ª∑
     */
    replyEvaluation(orderId) {
      uni.navigateTo({
        url: `/pages/rider/reply?orderId=${orderId}`
      })
    }
  }
}
</script>

<style scoped>
.evaluations-container {
  min-height: 100vh;
  background-color: #f5f5f5;
  padding-bottom: 120rpx;
  box-sizing: border-box;
}

/* Á≠õÈÄâÊ†è */
.filter-bar {
  display: flex;
  background: #fff;
  padding: 20rpx;
  gap: 10rpx;
  overflow-x: auto;
  white-space: nowrap;
  position: sticky;
  top: 0;
  z-index: 50;
}

.filter-item {
  flex-shrink: 0;
  padding: 12rpx 24rpx;
  background: #f5f5f5;
  border-radius: 40rpx;
  font-size: 26rpx;
  color: #666;
  transition: all 0.3s;
}

.filter-item.active {
  background: #07c160;
  color: #fff;
  font-weight: bold;
}

/* ËØÑ‰ª∑ÂàóË°® */
.evaluations-scroll {
  height: calc(100vh - 200rpx);
  padding: 20rpx;
}

.evaluation-list {
  display: flex;
  flex-direction: column;
  gap: 20rpx;
}

.evaluation-card {
  background: #fff;
  border-radius: 16rpx;
  padding: 30rpx;
  box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.05);
}

/* Áî®Êà∑‰ø°ÊÅØ */
.user-section {
  display: flex;
  align-items: center;
  margin-bottom: 20rpx;
}

.user-avatar {
  width: 80rpx;
  height: 80rpx;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 50%;
  color: #fff;
  font-size: 32rpx;
  font-weight: bold;
  margin-right: 20rpx;
}

.user-info {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.user-name {
  font-size: 30rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 8rpx;
}

.eval-time {
  font-size: 24rpx;
  color: #999;
}

/* ËØÑÂàÜ */
.rating-section {
  display: flex;
  align-items: center;
  margin-bottom: 20rpx;
  padding-bottom: 20rpx;
  border-bottom: 2rpx solid #f5f5f5;
}

.rating-stars {
  display: flex;
  gap: 4rpx;
  margin-right: 16rpx;
}

.star-icon {
  font-size: 32rpx;
  color: #ffd700;
}

.rating-score {
  font-size: 28rpx;
  font-weight: bold;
  color: #ff9800;
}

/* ËØÑ‰ª∑Ê†áÁ≠æ */
.tags-section {
  display: flex;
  flex-wrap: wrap;
  gap: 12rpx;
  margin-bottom: 16rpx;
}

.tag-item {
  padding: 8rpx 20rpx;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20rpx;
  font-size: 24rpx;
  color: #fff;
}

/* ËØÑ‰ª∑ÂÜÖÂÆπ */
.content-section {
  margin-bottom: 20rpx;
  padding-bottom: 20rpx;
  border-bottom: 2rpx solid #f5f5f5;
}

.content-text {
  font-size: 28rpx;
  color: #333;
  line-height: 1.8;
}

/* ËÆ¢Âçï‰ø°ÊÅØ */
.order-section {
  display: flex;
  flex-direction: column;
  gap: 12rpx;
  margin-bottom: 20rpx;
  padding-bottom: 20rpx;
  border-bottom: 2rpx solid #f5f5f5;
}

.order-info {
  display: flex;
  font-size: 26rpx;
}

.order-label {
  color: #999;
  flex-shrink: 0;
}

.order-value {
  color: #333;
  flex: 1;
}

/* È™ëÊâãÂõûÂ§ç */
.reply-section {
  padding: 20rpx;
  background: #f0f7ff;
  border-radius: 12rpx;
  margin-bottom: 20rpx;
}

.reply-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12rpx;
}

.reply-title {
  font-size: 26rpx;
  font-weight: bold;
  color: #1890ff;
}

.reply-time {
  font-size: 22rpx;
  color: #999;
}

.reply-content {
  font-size: 26rpx;
  color: #333;
  line-height: 1.6;
}

/* Êìç‰ΩúÂå∫Âüü */
.action-section {
  text-align: right;
}

.btn-reply {
  padding: 12rpx 32rpx;
  background: #07c160;
  color: #fff;
  font-size: 26rpx;
  border-radius: 40rpx;
  border: none;
}

.btn-reply::after {
  border: none;
}

/* Âä†ËΩΩÊõ¥Â§ö */
.load-more {
  padding: 30rpx;
  text-align: center;
  font-size: 26rpx;
  color: #999;
}

/* Á©∫Áä∂ÊÄÅ */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 120rpx 40rpx;
}

.empty-icon {
  font-size: 150rpx;
  margin-bottom: 30rpx;
  opacity: 0.5;
}

.empty-title {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 16rpx;
}

.empty-desc {
  font-size: 26rpx;
  color: #999;
}
</style>
